                    if(Math.sqrt((127-xVal)*(127-xVal) + (127-yVal)*(127-yVal)) + 10 <= 127){
                      that.context.clearRect(0, 0, that.colorPicker.width, that.colorPicker.height);
                      that.context.drawImage(img, 0, 0, that.colorPicker.width, that.colorPicker.height)
                      that.drawArc('#000000', 1.0, xVal*window.devicePixelRatio, yVal*window.devicePixelRatio, 10*window.devicePixelRatio, 0, 2 * Math.PI);
                      show = true
                    }
                  }
                }
              }
              console.log(that.colorPicker.offsetLeft + ' ' + (that.colorPicker.offsetTop + that.headerHeight.offsetTop) + ' ' + that.endPos.x + ' ' + that.endPos.y)
            }

            if(show){
              const imageDataStr = imageData[0] + imageData[1] + imageData[2] + imageData[3]
              if(imageDataStr == 0){
                return
              }else {
                if(that.fromPageName == "newLights"){
                  that.SwitchColour = 'rgb(' + imageData[0] + ',' + imageData[1] + ',' + imageData[2]+ ')';
                  that.SwitchRColour = imageData[0];
                  that.SwitchGColour = imageData[1];
                  that.SwitchBColour = imageData[2];
                }else if(that.fromPageName == "functionalMode"){
                  that.FunctionalRColour = imageData[0];
                  that.FunctionalGColour = imageData[1];
                  that.FunctionalBColour = imageData[2];
                  that.FunctionalColour = 'rgb(' + imageData[0] + ',' + imageData[1] + ',' + imageData[2]+ ')';
                }else if(that.fromPageName =="ambientMode"){
                  that.AmbientRColour = imageData[0];
                  that.AmbientGColour = imageData[1];
                  that.AmbientBColour = imageData[2];
                  that.AmbientColour = 'rgb(' + imageData[0] + ',' + imageData[1] + ',' + imageData[2]+ ')';
                }
              }
            }

          }, false)

          that.colorPicker.addEventListener('touchend', (e) => {
            let imageData = that.context.getImageData((that.endPos.x - that.colorPicker.offsetLeft)*window.devicePixelRatio, (that.endPos.y - that.colorPicker.offsetTop - that.headerHeight.offsetTop)*window.
            devicePixelRatio, 1, 1).data
            if(that.fromPageName == "newLights"){
              that.$fireBase.gaWebReport(that.from, 'move', 'newLights_rgb_light_colour',{"NewLightsRGBColour":'rgb(' + imageData[0] + ',' + imageData[1] + ',' + imageData[2]+ ')'})
            }else if(that.fromPageName == "functionalMode"){
              that.$fireBase.gaWebReport(that.from, 'move', 'functional_mode_rgb_light_colour',{"FunctionalRGBColour":'rgb(' + imageData[0] + ',' + imageData[1] + ',' + imageData[2]+ ')'})
            }else if(that.fromPageName =="ambientMode"){
              that.$fireBase.gaWebReport(that.from, 'move', 'ambient_mode_rgb_light_colour',{"AmbientRGBColour":'rgb(' + imageData[0] + ',' + imageData[1] + ',' + imageData[2]+ ')'})
            }
            // this.endPos.x = e.targetTouches[0].pageX - this.startPos.x
            // this.endPos.y = e.targetTouches[0].pageY - this.startPos.y
            // x = e.x - that.colorPicker.offsetLeft
