            // let imageData = that.context.getImageData(that.endPos.x - that.colorPicker.offsetLeft, that.endPos.y - that.colorPicker.offsetTop - that.headerHeight.offsetTop, 1, 1).data
            // if(imageData[0] == 0 && imageData[1] == 0 && imageData[2] == 0){
            //   console.log('other region')
            // } else {
            //   if(that.colorPicker.offsetLeft + 127 >= (that.endPos.x - that.colorPicker.offsetLeft ) && (that.endPos.x - that.colorPicker.offsetLeft) >= 0){
            //     if(that.colorPicker.offsetTop + that.headerHeight.offsetTop + 127 >= that.endPos.y - that.colorPicker.offsetTop - that.headerHeight.offsetTop){
            //       if(that.endPos.y - that.colorPicker.offsetTop - that.headerHeight.offsetTop >= 0){
            //         that.context.clearRect(0, 0, that.colorPicker.width, that.colorPicker.height);
            //         that.context.drawImage(img, 0, 0, that.colorPicker.width, that.colorPicker.height)
            //         that.drawArc('#000000', 0.5, that.endPos.x - that.colorPicker.offsetLeft, that.endPos.y - that.colorPicker.offsetTop - that.headerHeight.offsetTop, 10, 0, 2 * Math.PI);
            //       }
            //     } else if(that.colorPicker.offsetTop + that.headerHeight.offsetTop + 127 < that.endPos.y - that.colorPicker.offsetTop - that.headerHeight.offsetTop){
            //       if(that.endPos.y - that.colorPicker.offsetTop - that.headerHeight.offsetTop <= 254) {
            //         that.context.clearRect(0, 0, that.colorPicker.width, that.colorPicker.height);
            //         that.context.drawImage(img, 0, 0, that.colorPicker.width, that.colorPicker.height)
            //         that.drawArc('#000000', 0.5, that.endPos.x - that.colorPicker.offsetLeft, that.endPos.y - that.colorPicker.offsetTop - that.headerHeight.offsetTop, 10, 0, 2 * Math.PI);
            //       }
            //     }
            //   } else if(that.colorPicker.offsetLeft + 127 < (that.endPos.x - that.colorPicker.offsetLeft) &&  (that.endPos.x - that.colorPicker.offsetLeft) <= 254){
            //     if(that.colorPicker.offsetTop + that.headerHeight.offsetTop + 127 >= that.endPos.y - that.colorPicker.offsetTop - that.headerHeight.offsetTop){
            //       if(that.endPos.y - that.colorPicker.offsetTop - that.headerHeight.offsetTop >= 0) {
            //         that.context.clearRect(0, 0, that.colorPicker.width, that.colorPicker.height);
            //         that.context.drawImage(img, 0, 0, that.colorPicker.width, that.colorPicker.height)
            //         that.drawArc('#000000', 0.5, that.endPos.x - that.colorPicker.offsetLeft, that.endPos.y - that.colorPicker.offsetTop - that.headerHeight.offsetTop, 10, 0, 2 * Math.PI);
            //       }
            //     } else if(that.colorPicker.offsetTop + that.headerHeight.offsetTop + 127 < that.endPos.y - that.colorPicker.offsetTop - that.headerHeight.offsetTop){
            //       if(that.endPos.y - that.colorPicker.offsetTop - that.headerHeight.offsetTop <= 254) {
            //         that.context.clearRect(0, 0, that.colorPicker.width, that.colorPicker.height);
            //         that.context.drawImage(img, 0, 0, that.colorPicker.width, that.colorPicker.height)
            //         that.drawArc('#000000', 0.5, that.endPos.x - that.colorPicker.offsetLeft, that.endPos.y - that.colorPicker.offsetTop - that.headerHeight.offsetTop, 10, 0, 2 * Math.PI);
            //       }
            //     }
            //   }
            //   console.log(that.colorPicker.offsetLeft + ' ' + (that.colorPicker.offsetTop + that.headerHeight.offsetTop) + ' ' + that.endPos.x + ' ' + that.endPos.y)
            // }
            //
            // const imageDataStr = imageData[0] + imageData[1] + imageData[2] + imageData[3]
            // if(imageDataStr == 0){
            //   return
            // }else {
            //   if(that.fromPageName == "newLights"){
            //     that.SwitchColour = 'rgb(' + imageData[0] + ',' + imageData[1] + ',' + imageData[2]+ ')';
            //     that.SwitchRColour = imageData[0];
            //     that.SwitchGColour = imageData[1];
            //     that.SwitchBColour = imageData[2];
            //   }else if(that.fromPageName == "functionalMode"){
            //     that.FunctionalRColour = imageData[0];
            //     that.FunctionalGColour = imageData[1];
            //     that.FunctionalBColour = imageData[2];
            //     that.FunctionalColour = 'rgb(' + imageData[0] + ',' + imageData[1] + ',' + imageData[2]+ ')';
            //   }else if(that.fromPageName =="ambientMode"){
