          }, false)
        }
      }
      // this.colorPicker.addEventListener("touchend", function(event) {
      //   alert(event.x + ' ' + event.y)
      //   document.addEventListener("touchend", that.touchend, false);
      // })
    },
    computed: {
      commonHeaderBg(){
        return { 'background': '#e9e9e9' }
      }
    },
    methods: {
      touchstart(e){
        let touchnow = false
        let x = 0
        let y = 0
        this.colorPicker.addEventListener('touchstart', (e) => {
          touchnow = true
          x = e.x - this.colorPicker.offsetLeft
          y = e.y - this.colorPicker.offsetTop - this.headerHeight.offsetTop
        }, false)
      },
      touchend(e){
        let touchnow = false
        alert(e.targetTouches[0] + ' ' + e.y)
        let x = e.x - this.colorPicker.offsetLeft
        let y = e.y - this.colorPicker.offsetTop - this.headerHeight.offsetTop
      },

      drawArc(color, lineWidth, stepX, stepY, stepR, startAngle, endAngle) {
        this.context.strokeStyle = color;
        this.context.lineWidth = lineWidth;
        this.context.beginPath();
        this.context.arc(stepX, stepY, stepR, startAngle, endAngle);
        this.context.stroke();
      },

      // 返回灯光设置主界面
      goLight(){
        this.goPath();
      },
      goPath(){
        let params = { language: this.$i18n.locale,from: this.from, deviceId: this.deviceId }
        if(this.fromPageName=="newLights"){
          this.$commonMethods.routerGo(this.$router,'/newLights/newLights', params)
        }else if(this.fromPageName=="functionalMode"){
          this.$commonMethods.routerGo(this.$router, '/newLights/functionalMode', params)
        }else if(this.fromPageName=="ambientMode"){
           this.$commonMethods.routerGo(this.$router,'/newLights/ambientMode', params)
        }
      },
      //历史选择颜色值直接选定
      checkAmbientColor(v){
        if(this.fromPageName == "newLights"){
          this.SwitchColour = v
          const rgb = this.SwitchColour
          const regexp =  /[0-9]{0,3}/g
          const re = rgb.match(regexp)
          for(let i = 0;i < re.length; i++){
            if(re[i]==""){
              re.splice(i,1)
              i--
            }
          }
          this.SwitchRColour = re[0];
          this.SwitchGColour = re[1];
          this.SwitchBColour = re[2];
        }else if(this.fromPageName=="functionalMode"){
          this.FunctionalColour = v
          const rgb = this.FunctionalColour;
          const regexp =  /[0-9]{0,3}/g
          const re = rgb.match(regexp)
          for(let i = 0;i < re.length; i++){
            if(re[i]==""){
              re.splice(i,1)
              i--
            }
          }
          this.FunctionalRColour = re[0];
          this.FunctionalGColour = re[1];
          this.FunctionalBColour = re[2];
        }else if(this.fromPageName == "ambientMode"){
          this.AmbientColour = v
          const rgb = this.AmbientColour;
          const regexp =  /[0-9]{0,3}/g
          const re = rgb.match(regexp)
          for(let i = 0;i < re.length; i++){
            if(re[i]==""){
              re.splice(i,1)
              i--
            }
          }
          this.AmbientRColour = re[0];
          this.AmbientGColour = re[1];
          this.AmbientBColour = re[2];
        }
      },
      //选中确定跳转
      useSelect(){
        // this.showLoading = true;
        this.$store.commit('lights/X_INDEX', this.xIndex);
        this.$store.commit('lights/Y_INDEX', this.yIndex);
        if(this.fromPageName == "newLights"){
          this.$store.commit('lights/SWITCH_R_COLOUR', this.SwitchRColour);
          this.$store.commit('lights/SWITCH_G_COLOUR', this.SwitchGColour);
          this.$store.commit('lights/SWITCH_B_COLOUR', this.SwitchBColour);
          this.$store.commit('lights/SWITCH_COLOUR', this.SwitchColour);
          let params={
            SwitchRColour:this.SwitchRColour,
            SwitchGColour:this.SwitchGColour,
            SwitchBColour:this.SwitchBColour
          };
          this.$native.sendCmd(this.from,this.nativeKey,this.deviceId,JSON.stringify(params));
          if(this.from == "html"){
            let res={
              "resultCode":'0'
            }
            this.sendCmdCallback(res);
          }
        }
        else if(this.fromPageName == "functionalMode"){
          this.$store.commit('lights/FUNCTIONAL_R_COLOUR', this.FunctionalRColour);
          this.$store.commit('lights/FUNCTIONAL_G_COLOUR', this.FunctionalGColour);
          this.$store.commit('lights/FUNCTIONAL_B_COLOUR', this.FunctionalBColour);
          this.$store.commit('lights/FUNCTIONAL_COLOUR', this.FunctionalColour);
          let params={
            FunctionalRcolour:this.FunctionalRColour,
            FunctionalGcolour:this.FunctionalGColour,
            FunctionalBcolour:this.FunctionalBColour,//key值c小写
          }
          console.log("this.params功能模式入参",params);
          this.$native.sendCmd(this.from,this.nativeKey,this.deviceId,JSON.stringify(params));
          if(this.from == "html"){
            let res={
              "resultCode":'0'
            }
            this.sendCmdCallback(res);
          }
        }else if(this.fromPageName == "ambientMode"){
          this.$store.commit('lights/AMBIENT_R_COLOUR', this.AmbientRColour);
          this.$store.commit('lights/AMBIENT_G_COLOUR', this.AmbientGColour);
          this.$store.commit('lights/AMBIENT_B_COLOUR', this.AmbientBColour);
          this.$store.commit('lights/AMBIENT_COLOUR', this.AmbientColour);
          let params={
            AmbientRcolour:this.AmbientRColour,
            AmbientGcolour:this.AmbientGColour,
            AmbientBcolour:this.AmbientBColour
          }
          console.log("this.params氛围模式入参",params);
          this.$fireBase.gaWebReport(this.from, 'sendCmd', 'ambient_mode_RGB_light_colour',JSON.stringify(params))
          this.$native.sendCmd(this.from,this.nativeKey,this.deviceId,JSON.stringify(params));
          if(this.from == "html"){
            let res={
              "resultCode":'0'
            }
            this.sendCmdCallback(res);
          }
        }
      },
      sendCmdCallback(data){
        console.log("sendCmdCallback返回数据=====",data);
        this.showLoading = false;
        if(data.resultCode == '0' || data.resultCode == 0){
          this.$toast(this.$t('lang.success'));
          let params = {from: this.from,language: this.$i18n.locale, deviceId:this.deviceId}
          if(this.fromPageName=="newLights"){
            this.$commonMethods.routerGo(this.$router, '/newLights/newLights', params);
          }else if(this.fromPageName=="functionalMode"){
            this.$commonMethods.routerGo(this.$router,'/newLights/functionalMode', params);
          }else if(this.fromPageName=="ambientMode"){
            this.$commonMethods.routerGo(this.$router,'/newLights/ambientMode', params);
          }
        }else{
          this.$toast(this.$t('lang.failure'));
        }
      },
      //取消跳转
      cancelChoose(){
        this.goPath()
      },
      //物理返回键
      onBackPressedCallback(){
        this.goLight()
      }
    },
    components: {
      commonHeader: () => import('@/components/common/commonHeader'),
      loading: () => import('@/components/common/loading')
    }
  }
</script>

<style lang="less">
  /* 头部 */
  #color_selected .common_header_left {
    position: absolute;
    top: 0;
    left: 0;
    width: 19%;
    height: 44px;
    overflow: hidden;
    cursor: pointer;
    i {
      display: block;
      margin: 14px auto 14px 20px;
      font-size: 16px;
      font-family: Arial;
    }
  }
  /* 主体 */
  #color_selected .color_select_content {
    padding: 100px 0 52px;
    height: 100vh;
    background: #fff;
    /*overflow-y: scroll;*/
    /*-webkit-overflow-scrolling: touch;*/
    .color_select_wrap {
      padding-bottom: 52px;
      min-height: calc(100% + 1px);
      background: #fff;
      h3 {
        width: 83%;
        margin: 20px auto 16px;
        font-size: 18px;
        color: #000;
        font-family: Arial;
      }
      .ambient_color_select {
        width: 83%;
        height: 24px;
        margin: 0 auto 50px;
        -webkit-border-radius: 12px;
        -moz-border-radius: 12px;
        -ms-border-radius: 12px;
        -o-border-radius: 12px;
        border-radius: 12px;
        -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
        -moz-box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
        -ms-box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
        -o-box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
        box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
      }
      .color_select {
        /*position: absolute;*/
        display: block;
        width: 100%;
        margin: 0 auto 40px;
        overflow: hidden;
        .colorCanvas {
          position: relative;
          display: block;
          margin: 0 auto;
          /*z-index: 1;*/
        }
        .color_select_img {
          /*position: relative;*/
          display: none;
          width: 10px;
          height: 10px;
          top:0;
          left:0;
          /*z-index: 10;*/
        }
      }
      .color_select_history {
        display: flex;
        justify-content: space-between;
        width: 83%;
        height: 36px;
        margin: 0 auto;
        li {
          width: 36px;
          height: 36px;
          -webkit-border-radius: 50%;
          -moz-border-radius: 50%;
          -ms-border-radius: 50%;
          -o-border-radius: 50%;
          border-radius: 50%;
          cursor: pointer;
        }
      }
    }
  }
  /* 底部操作 */
  #color_selected .color_select_btn {
    position: fixed;
    display:flex;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 64px;
    padding: 8px;
    overflow: hidden;
    z-index: 2;
    font-weight: bold;
    font-size: 14px;
    font-family: Arial;
    background: #fff;
    -webkit-box-shadow: 0 0 6px rgba(0,0,0,0.15);
    -moz-box-shadow: 0 0 6px rgba(0,0,0,0.15);
    -ms-box-shadow: 0 0 6px rgba(0,0,0,0.15);
    -o-box-shadow: 0 0 6px rgba(0,0,0,0.15);
    box-shadow: 0 0 6px rgba(0,0,0,0.15);

    .btn_box{
      width:100%;
      height: 48px;
      display:flex;
    }
    .btn_use{
      color: white;
      width:50%;
      line-height: 48px;
      background: #00B0A9;
      text-align: center;
      border-radius: 0 5px 5px 0;
    }
    .btn_cancel{
      width:50%;
      line-height: 48px;
      background: #DCDCDC;
      text-align: center;
      border-radius: 5px 0 0 5px;
    }
    .btn{
      padding: 15 16px;
    }
  }


</style>



